@import url(//db.onlinewebfonts.com/c/2f56ec659663c922a1a776c3a3760529?family=Ginra+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Title {
    background-image: url("/background.png");
    justify-content: center;
    vertical-align: auto;
    height: 100vh;
}


@layer one {
    .Title h1 {
        position:relative;
        z-index: 1;
        padding-top: 400px;
        text-align: center;
        font-size: 300%;
        font-family: "Ginra Sans";
        color: white;
    }

    .Title h1 span {
        color: darkblue;
    }
}

@layer two {

    .Title #quote p {
        text-align: center;
        color: white;
        font-size: 200%;
        font-style: italic;
        -webkit-transform: translate(0, 10vh);
                transform: translate(0, 10vh);
        transition: all 1s;
    }
    .Title .quote-idle p {
        position: relative;
        opacity: 0;
    }
    .Title .quote-disappear p{
        position:relative;
        opacity: 0;
        -webkit-transform: translate(0, 10vh);
                transform: translate(0, 10vh);
    }

    .quote-appear {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

.navbar {
    background-color: #1a202c;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

.navbar ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    text-align: center;
}

.navbar li {
    margin: 10%;
}

.navbar .elem a {
    border-radius: 10px;
    padding: 20%;
    transition-property: all;
    transition-duration: 0.7s;
}

a {
    text-decoration: none;
    color:white;
}

.navbarElem .logo-elem {
    padding-right: 100%;
}

.navbar .user-elem{
    padding-left: 100%;
}

.navbar .elem:hover a{
    background-color: white;
    color: #1a202c;
}

body {

    background-color: #16161A;
}
